<template >
<div class="more_movieslist">
    <div class="more_movie" v-for="(moremovie,id) in moviesA" :key="id">
        <img class="more_movie-img" :src= moremovie.images.medium>
        <p class="more_movie_title">{{moremovie.title}}</p>
        <div class="more_strats-container">
            <div class="more_not_pos">
                <img class="more_notstart" src="/static/images/icon/none-star.png" v-for="startitem in 5" :key="startitem.id">
                <i class="more_grade">{{moremovie.rating.average}}</i>
            </div>
            <div class="more_has_pos">
                <img class="more_hasstart" src="/static/images/icon/star.png" v-for="imgitem in moremovie.rating.stars/10" :key="imgitem.id">
            </div>
        </div>
    </div>
</div>
</template>
<script>
import '../../assets/js/jquery-3.3.1.js'
import movieA from './movies.vue'
export default {
    name:'MovieMore',
    data(){
        return{
            moviesA:[],
            moviesB:[],
            moviesC:[],
            // cha:[moviesA, moviesB,moviesC],
            i: 0
        }
    },
    methods:{
        getdata(url,moviesdata){
            $.ajax({
                url: url,
                type: 'get',
                dataType: 'jsonp',//è§£å³è·¨åé®é¢
                success: function(data){
                    moviesdata = data.subjects;
                    console.log(moviesdata);
                }
            })
        }
    },
    mounted(){
        let url_A = 'https://douban.uieee.com/v2/movie/in_theaters';
        let url_B = 'https://douban.uieee.com/v2/movie/coming_soon';
        let url_C = 'https://douban.uieee.com/v2/movie/top250';

        this.getdata(url_A,this.moviesA);
        // this.getdata(url_B,this.moviesB);
        // this.getdata(url_C,this.moviesC);
        // console.log(movieA.data());
        // console.log(this.moviesA);
    }
}
</script>
<style lang="less" scoped>
 .more_movieslist{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        padding-top: 10px;
        .more_movie{
            padding: 0px;
            width: 95px;
            margin: 0px 10px;
            // overflow: hidden;
            .more_movie-img{
                width: 95px;
                height: 130px;
                margin: 0px;
            }
            .more_movie_title{
                margin: 0px;
                font-size: 10px;
                font-family: 'å¾®è½¯éé»','Times New Roman', Times, serif;
                overflow: hidden;
                white-space:nowrap;
            }
            .more_strats-container{
                margin: 0px;
                .more_has_pos{
                    position: relative;
                    top: -18px;
                    margin: 0px;
                    // display: inline;
                    .more_hasstart{
                        width: 8px;
                        height: 8px;
                        margin:0px 2px;
                        z-index: 50;
                    }
                }
                .more_not_pos{
                    // display: inline;
                    .more_hasstart{
                        width: 8px;
                        height: 8px;
                        margin:0px 2px;
                        z-index: 50;
                    }
                }
                .more_notstart{
                    width: 8px;
                    height: 8px;
                    margin:0px 2px;
                    z-index: -50;
                }
                .more_grade{
                    font-size: 6px;
                }
            }
        }
    }
</style>


